* {
  box-sizing: border-box;
}

.CV{
  background-color: #eff1f5;
  width: 100vw;
  max-width: 100vw;
  overflow-x: hidden;
  .menu{
    text-align: center;
    align-items: center;
    justify-content: center;
    box-shadow: none;
    margin-bottom: -70px;
    background-color: rgb(134 144 154 / 15%);
  // margin-bottom: -70px;
  // background-color: rgba(8, 26, 44, 0.15);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    display: flex;  
    position: relative;
    z-index: 2;
    width: 100vw;
    height: 64px;
    background-color: 0 0 4px rgba(0, 0, 0, 0.2);
    
    box-shadow: var(--base-shadow);
          //flex-wrap: wrap;
    .logo{
      width:90px;
      height: 33px;
      margin-right: 20px;
      
      overflow-clip-margin: content-box;
      overflow: clip;
      background-size:contain;
      background-position:top;
      background: cover; 
      background-image: url('../images/logo_dark.svg'); 
    }
    .link{
      //color: rgb(241, 236, 236);
      margin-right: 20px;
      text-decoration: none;
      font-size: 16px;
      font-weight: bold;
      color: white;

    }
    .second_link{
      margin-left: 100px;
      .link2{
        //color: rgb(241, 236, 236);
        margin-right: 20px;
        text-decoration: none;
        font-size: 16px;
        font-weight: bold;
        //color: white;
        color: #ffffff;
        padding: 1.3175rem 1rem;

      }
      .donate{
        background-color: #eff1f5;
        border-radius: 9px;
        box-shadow: inset 0 0 0 2px #ef6767;
        padding: 13px;
        text-decoration: none;
        color: #ef6767;
      }
    }  

  }
  // padding:100px;
  .first{
    
    width:100%;
    height: 100vh;
    //box-sizing:border-box;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-repeat:no-repeat;
    background-position:center center;
    background-blend-mode: darken;
    // background-size: cover;
    background-color:rgba(#151424, .4);
    background-image: url('../images/godot.jpg') ;
    .container{
      width: 80%;
      justify-content: space-between;
      display: flex;
      flex-wrap: wrap;
      margin: auto;
      align-items: center;
      margin-top: 0px;

      .cover_font{
        margin-top: 100px;
        margin-left: 20px;
        .font1{
          // margin-left: -650px;
          font-size: 50px;
          color: rgb(238, 231, 231);
          
          
        }
        .font2{
        //  margin-left: -650px;
          //top: 50px;
          font-size: 20px;
          color: rgb(235, 228, 228);
          
          position: relative;
        }
    
      }
    
      .cover_mypic{
        right: 50px;
    
        height: 400px;
        width: 320px;
        background-color: rgb(253, 252, 252);
        //left: 60%;
        right: 0;
        top:120px;
        border-radius:100px 100px 100px 100px;
        text-align: center;
        position: relative;

        .my_picture{
          border-radius:100px 100px 100px 100px;
          width:300px;
          height:400px;

        }
      }

    }
    

  }
  .page2{
    padding-top: 64px;
    padding-bottom: 64px;
    max-width: 1200px;
    margin: auto;
    overflow: visible;
    width: auto;
    padding-left: 16px;
    padding-right: 16px;
    
    .latest{
      margin: 0px 0px 29.88px;
      font-size: 36px;
      font-family: "Montserrat", sans-serif;
      font-weight: 800;
      color: #546b99;
      margin-top: 0px;
    }
    .box_page2{
      width: auto;
      padding-left: 16px;
      padding-right: 16px;
      gap:30px;
       margin: auto;
       overflow: visible;
    
       max-width: 1200px;
       display: flex;
       flex-wrap: wrap;


      .container_page2{
        height: 100%;
        display: flex;
        flex-direction: column;
        
        filter: brightness(100%);
        flex: 1;
        grid-template-columns: 160px 1fr;
        //width: 50%;
       
        .image_page2{
          width: 100%;
          gap: 20px;
          //background-image: url(../images/maintenance-release-godot.jpg);
          aspect-ratio: 16 / 9;
          border-radius: 7px;
          background-position: center;
          background-size: cover;
          background-color:#ffffff;
          box-shadow: 0 5px 10px -3px #00000078;
          grid-template-columns: 160px 1fr;
        }
        .box_content{
         
          .main{
            font-family:  "Montserrat", sans-serif;
            font-weight: 800;
            color: #546b99;
            margin-top: 0px;
            display: block;
            font-size: 1.17em;
            margin-block-start: 1em;
            margin-block-end: 1em;
            margin-inline-start: 0px;
            margin-inline-end: 0px;
            font-weight: bold;
          }
          .write_box_content{
            color: #4a5365;
            font-size: 16px;
            opacity: 0.8;
            margin-top: 0;
          }
          
        }
      }
      .container_page2_2{
        flex: 1; 
        display: grid;
        height: 100%;
        gap:20px;
      
       
       
       .new_list{
          overflow-x: hidden;
          display: grid;
          gap:18px;
          // grid-template-columns: 80px 1fr;
          grid-template-columns: 160px 1fr;
          height: 100%;
          .first_pic{
           
            width: 100%;
            aspect-ratio: 16 / 9;
            border-radius: 7px;
            background-position: center;
            background-size: cover;
            background-color: var(--card-background-color);
            box-shadow: 0 5px 10px -3px #00000078;
            grid-template-columns: 160px 1fr;
               
           }
         .title_article{
            font-family: "Montserrat", sans-serif;
            font-weight: 800;
            color: #546b99;
            margin-top: 0px;
            font-size: 22px;
            margin-bottom: 10px;
          }
          .first_article{
            color: #4a5365;
            font-size: 16px;
            opacity: 0.8;
            margin-top: 0;
            text-rendering: optimizeLegibility;
            line-height: 1.5;
            
          }
        }
      }
    }
    
  }
  
  .page3{
    width: auto;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 64px;
    padding-bottom: 64px;
    max-width: 1200px;
    margin: auto;
    overflow: visible;
    //display: block;
    .font_page3{
      margin: 0px 0px 29.88px;
      font-size: 36px;
      font-family: "Montserrat", sans-serif;
      font-weight: 800;
      color: #546b99;
      margin-top: 0px;
    }
    .wall_page3{
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto 1fr auto;
      //height: 100vh;
      gap: 20px;
      column-count: 3;
      .background_page3{
        background-color: rgb(51 63 103 / 90%);
        position: relative;
        overflow: hidden;
        transition: transform 200ms ease-in-out;
        height: 100%;
        border-radius: 8px;
        
        .image_page3{
         
          width: 100%;
          height: auto;
          position: relative;
          background-color: #607080;
          aspect-ratio: 3/2;
          object-fit: cover;
          object-position: top center;
          overflow-clip-margin: content-box;
          overflow: clip;
          
        }
        .wrap_articles{
          padding: 24px;
          .title_image_page3{
         
            margin-bottom: 10px;
            color: white;
            font-size: 22px;
             font-family: "Montserrat", sans-serif;
             font-weight: 800;
            // color: #546b99;
             margin-top: 0px;
          }
          .article_page3{
           
            margin: 0;
            text-rendering: optimizeLegibility;
            line-height: 1.5;
            color: lightsteelblue;
            
            margin-block-start: 1em;
            margin-block-end: 1em;
            margin-inline-start: 0px;
            margin-inline-end: 0px;
          }
        }
       
      }
     
    }
    .cover_button_page3{
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 16px;
      .button_page3{
        box-shadow: none;
        background-color: rgb(111 111 111 / 27%);
        -webkit-backdrop-filter: blur(4px);
        backdrop-filter: blur(4px);
        color: hsla(0, 0%, 100%, 0.95);
        margin: 16px 32px 16px 32px;
        font-size: 18px;
        padding: 16px 32px 16px 32px;
        display: inline-block;
        text-decoration: none;
      }
    }
    
    
  }
  .page4{
    padding-top: 64px;
    padding-bottom: 64px;
    max-width: 1200px;
    margin: auto;
    overflow: visible;
    width: auto;
    padding-left: 16px;
    padding-right: 16px;
    //display: block;

    .font_page4{
      font-size: 36px;
      font-family: "Montserrat", sans-serif;
      font-weight: 800;
      color: #546b99;
      margin-top: 0px;
      margin: 0px 0px 29.88px;
    }
    .after_font_page4{
      text-rendering: optimizeLegibility;
      line-height: 1.5;
      color: #4A5365;
      font-size: 19.7417px;
      font-family: sans;
     
    }
    .picture_flex{
      
      box-sizing: border-box;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      grid-template-rows: auto 1fr auto;
      gap: 20px;
      column-count: 3;
     
    
      .cover_picture_page4{
        max-width: 1200px;
        margin: auto;
        overflow: visible;
       
        flex: 1;
        text-align: center;
        padding: 18px;
        overflow-clip-margin: content-box;
          overflow: clip;
        
        .pictures_page4{
          width: 250px;
          aspect-ratio: auto 250 / 250;
          height: 250px;
          
          text-align: center;
        }
        .title_content_page4{
          font-size: 22px;
          font-family: "Montserrat", sans-serif;
          font-weight: 800;
          color: #546b99;
          margin: 0px 0px 29.26px;
          
  
        }
        .content_page4{
          text-rendering: optimizeLegibility;
          line-height: 1.5;
          color: #4A5365;
          margin: 18px 0px;
          display: block;
          margin-block-start: 1em;
          margin-block-end: 1em;
          margin-inline-start: 0px;
          margin-inline-end: 0px;
        
        }
      }
    
    }
   
    
  }
  .page5{
   
    background-color: #333f67;
    color: lightsteelblue;
    overflow: auto;
    text-align: center;
    // width: auto;
    // // padding-left: 16px;
    // //padding-right: 16px;
    padding-top: 64px;
    padding-bottom: 64px;
   
   
    .content4{
      font-family: "Montserrat", sans-serif;
      width: 1200px;
      max-width: 1200px;
      margin: auto;
      overflow: visible;
      width: auto;
      padding-left: 16px;
      padding-right: 16px;
      //margin: 0px;
      .tree{
        max-width: 400px;
        height: auto;
        aspect-ratio: auto 1 / 1;
        overflow-clip-margin: content-box;
        overflow: clip;
      }
      .donate{
        color: white;
        font-size: 28px;
        // /margin: 0px 0px 28px;
        font-weight: 800;
      }
      .article_page5{
       max-width: 500px;
        margin: auto;
        font-size: 18px;
       /// margin : 0px 350px;
   
      }
    }
   
  }
  .page6{
    padding-top: 64px;
    padding-bottom: 64px;
    max-width: 1200px;
    margin: auto;
    overflow: visible;
    width: auto;
    padding-left: 16px;
    padding-right: 16px;
    // padding-top: 64px;
    // padding-bottom: 64px;
    // width: 1200px;
    // max-width: 1200px;
    // margin: auto;
    // overflow: visible;
    .font6{
      padding-left: 0px;
      text-align: center;
      font-size: 36px;
      font-family: "Montserrat", sans-serif;
      font-weight: 800;
      color: #546b99;
      margin-top: 0px;
    }
    .sponsored{
      justify-content: center;
      flex-wrap: wrap;
      display: flex;
      .sponsored_grey{
        // background-color: #cfdbe4;
        // border-color: #cfdbe4;
        background-color: #dcdcdc;
        border: 14px solid #dcdcdc;
        border-radius: 8px 8px;
        
        align-items: center;
        height: 80px;
        box-sizing: border-box;
        margin-bottom: 8px;
        margin-left: 8px;
        overflow: auto;
        opacity: 0.85;
        transition: opacity 0.2s;
        height: 100px;
        .pic_s{
          mix-blend-mode: multiply;
          width: inherit;
          height: 100%;
          overflow-clip-margin: content-box;
          overflow: clip;
        }
        
      }
      .sponsored_white{
        background-color: #dcdcdc;
        border: 14px solid #dcdcdc;
        border-radius: 8px 8px;
        display: flex;
        align-items: center;
        height: 80px;
        box-sizing: border-box;
        margin-bottom: 8px;
        margin-left: 8px;
        overflow: auto;
        opacity: 0.85;
        transition: opacity 0.2s;
        height: 68px;
        
        .pic_w{
          mix-blend-mode: multiply;
          width: inherit;
          height: 100%;
          overflow-clip-margin: content-box;
          overflow: clip;
          
        }
      }
      
      
    }
  }
  .page7{
    background-color: #414d69;
    color: rgb(175 180 220);
    font-size: 0.9em;
    margin-top: 64px;
    padding-top: 32px;
    padding-bottom: 32px;
    display: block;
    .cover_page7{
      width: auto;
      padding-left: 16px;
      padding-right: 16px;
      display: flex;
      max-width: 1200px;
      margin: auto;
      overflow: visible;
      .one{
        margin-top: 0;
        
        flex: 2;
        padding-left: 0px;
        // display: block;
        // margin-block-start: 1em;
        // margin-block-end: 1em;
        // margin-inline-start: 0px;
        // margin-inline-end: 0px;
        .linkone{
          font-size: 16.2px;
          color: rgb(200 210 250);
          text-decoration-color: rgb(200 210 250 / 30%);
        }
      }
      .wraptwo{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 24px;
        margin-left: 16px;
        .two{
       
          text-rendering: optimizeLegibility;
          line-height: 1.5;
          padding-left: 2rem;
          float: left;
          list-style: none;
          margin: 0px;
          overflow-wrap: break-word;
        
          .title7{
            line-height: 20px;
            margin-bottom: 10px;
            display: list-item;
            text-align: -webkit-match-parent;
            .strong{
              color: rgb(200 210 250);
              font-weight: bold;
              font-size: 16.2px;
            }
            .linktwo{
              font-size: 16.2px;
              color: rgb(200 210 250);
              text-decoration-color: rgb(200 210 250 / 30%);
            }
          }
        }
        
      }
      .three{
        flex: 1;
        margin-left: 32px;
        .titlethree{
          font-size: 22px;
          font-weight: 800;
          text-align: right;
        }
      }
      
    }
    
  }

}  
